 ($qualifier == max-width) {\n            @media only screen and (orientation: $check-orientation) and (max-width: $size) {\n                @content;\n            }\n        }\n    }\n}\n\n@mixin bounded-select($orientation, $check-orientation, $min, $max) {\n    @if $orientation == none or $orientation == $check-orientation {\n        @media only screen and (orientation: $check-orientation) and (min-width: $min) and (max-width: $max) {\n            @content;\n        }\n    }\n}\n\n// this is a copy of respond-to. we don't want to preserve that method since the breakpoints may diverge from the old UI.\n@mixin is-screen-size($media: all, $orientation: none) {\n    @if $media == mobile {\n        @include query-select($orientation, portrait, $device-mobile-size, max-width) {\n            @content;\n        }\n        @include query-select($orientation, landscape, $device-landscape-mobile-size, max-width) {\n            @content;\n        }\n    } @else if $media == not-mobile {\n        @include query-select($orientation, portrait, $device-mobile-size + 1, min-width) {\n            @content;\n        }\n        @include query-select($orientation, landscape, $device-landscape-mobile-size + 1, min-width) {\n            @content;\n        }\n    } @else if $media == laptop {\n        @include bounded-select($orientation, portrait, $device-tablet-size + 1, $device-laptop-size) {\n            @content;\n        }\n        @include bounded-select($orientation, landscape, $device-landscape-tablet-size + 1, $device-landscape-laptop-size) {\n            @content;\n        }\n    } @else if $media == tablet {\n        @include bounded-select($orientation, portrait, $device-mobile-size + 1, $device-tablet-size) {\n            @content;\n        }\n        @include bounded-select($orientation, landscape, $device-landscape-mobile-size + 1, $device-landscape-tablet-size) {\n            @content;\n        }\n    } @else if $media == desktop {\n        @include query-select($orientation, portrait, $device-tablet-size, min-width) {\n            @content;\n        }\n        @include query-select($orientation, landscape, $device-landscape-tablet-size, min-width) {\n            @content;\n        }\n    } @else if $orientation == landscape {\n        @media only screen and (orientation: landscape) {\n            @content;\n        }\n    } @else if $orientation == portrait {\n        @media only screen and (orientation: portrait) {\n            @content;\n        }\n    }\n}\n\n\n@mixin hide-text-overflow {\n    white-space: nowrap;\n    overflow: hidden;\n    text-overflow: ellipsis;\n}\n\n\n@mixin page-padding {\n    padding-left: $spacing-small;\n    padding-right: $spacing-small;\n}\n\n// Font Mixins\n$font-mckinsey-sans: \"McKinsey Sans\";\n$font-theinhardt: \"Theinhardt\", \"Helvetica Neue\", Arial, sans-serif;\n$font-bower: \"Bower\";\n\n@mixin bower-bold {\n    font-family: $font-bower;\n    font-style: normal;\n    font-weight: bold;\n    text-align: center;\n}\n\n@mixin mckinsey-sans {\n    font-family: $font-mckinsey-sans;\n    font-style: normal;\n    text-align: center;\n}\n\n@mixin display-1 {\n    @include bower-bold;\n    font-size: 3.75rem; // 60px\n    line-height: 1.2; // 72px\n\n    @include is-screen-size(not-mobile) {\n        font-size: 4rem; // 64px\n        line-height: 1.187; // 76px\n    }\n}\n\n@mixin display-2 {\n    @include bower-bold;\n    font-size: 3.5rem; // 56px\n    line-height: 1.214; // 68px\n\n    @include is-screen-size(not-mobile) {\n        font-size: 3.75rem; // 60px\n        line-height: 1.2; // 72px\n    }\n}\n\n@mixin display-3 {\n    @include bower-bold;\n    font-size: 3.25rem; // 52px\n    line-height: 1.153; // 60px\n\n    @include is-screen-size(not-mobile) {\n        font-size: 3.5rem; // 56px\n        line-height: 1.214; // 68px\n    }\n}\n\n@mixin display-4 {\n    @include bower-bold;\n    font-size: 3rem; // 48px\n    line-height: 1.166; // 56px\n\n    @include is-screen-size(not-mobile) {\n        font-size: 3.25rem; // 52px\n        line-height: 1.153; // 64px\n    }\n}\n\n@mixin display-5 {\n    @include bower-bold;\n    font-size: 2rem; // 30px\n    line-height: 1.833; // 34px\n\n    @include is-screen-size(not-mobile) {\n        font-size: 2.25rem; // 36px\n        line-height: 1.642; // 40px\n    }\n}\n\n@mixin display-7 {\n    @include bower-bold;\n    font-size: 1.5rem; // 24px\n    line-height: 1.333; // 28px\n\n    @include is-screen-size(not-mobile) {\n        font-size: 1.75rem; // 28px\n        line-height: 1.142; // 32px\n    }\n}\n\n@mixin heading-1 {\n    @include mckinsey-sans;\n    font-size: 2.5rem; // 40px\n    line-height: 1.2; // 48px\n\n    @include is-screen-size(not-mobile) {\n        font-size: 2.75rem; // 44px\n        line-height: 1.181; // 52px\n    }\n}\n\n@mixin heading-2 {\n    @include mckinsey-sans;\n    font-size: 2.25rem; // 36px\n    line-height: 1.111; // 44px\n\n    @include is-screen-size(not-mobile) {\n        font-size: 2.5rem; // 40px\n        line-height: 1.181; // 48px\n    }\n}\n\n@mixin heading-3 {\n    @include mckinsey-sans;\n    font-size: 2rem; // 32px\n    line-height: 1.125; // 36px\n\n    @include is-screen-size(not-mobile) {\n        font-size: 2.25rem; // 36px\n        line-height: 1.222; // 44px\n    }\n}\n\n@mixin heading-4 {\n    @include mckinsey-sans;\n    font-size: 1.75rem; // 28px\n    line-height: 1.142; // 32px\n\n    @include is-screen-size(not-mobile) {\n        font-size: 2rem; // 32px\n        line-height: 1.125; // 36px\n    }\n}\n\n@mixin heading-5 {\n    @include mckinsey-sans;\n    font-size: 1.5rem; // 24px\n    line-height: 1.333; // 288px\n\n    @include is-screen-size(not-mobile) {\n        font-size: 1.75rem; // 28px\n        line-height: 1.142; // 32px\n    }\n}\n\n@mixin heading-6 {\n    @include mckinsey-sans;\n    font-size: 1.25rem; // 20px\n    line-height: 1.4; // 24px\n\n    @include is-screen-size(not-mobile) {\n        font-size: 1.5rem; // 24px\n        line-height: 1.155; // 28px\n    }\n}\n\n@mixin body-default {\n    @include mckinsey-sans;\n    font-size: 1rem; // 16px\n    line-height: 1.4; // 24px\n\n    @include is-screen-size(not-mobile) {\n        font-size: 1.25rem; // 20px\n        line-height: 1.6; // 26px\n    }\n}\n\n/// Doesnt exist in MDS.\n@mixin body-tiny {\n    @include mckinsey-sans;\n    font-size: 0.75rem; // 12px\n    line-height: 1.4; // 20px\n\n    @include is-screen-size(not-mobile) {\n        font-size: 1rem; // 16px\n        line-height: 1.6; // 24px\n    }\n}\n\n@mixin form-label {\n    font-style: normal;\n    text-align: center;\n    font-family: $font-theinhardt;\n    font-size: 1rem;\n    line-height: 1.5rem;\n}\n\n@mixin placeholder-label {\n    @include form-label;\n    text-align: left;\n    color: $placeholder-color;\n    font-weight: 300;\n}\n\n@mixin chip-label {\n    @include mckinsey-sans;\n    font-size: 0.75rem;\n    line-height: 1.5rem;\n}\n\n@mixin checkbox-label {\n    @include form-label;\n    font-weight: 300;\n    color: black;\n}\n\n// borders\n\n@mixin mck-checkbox-border {\n    border: 1px solid $border;\n    border-radius: 0;\n}\n\n@mixin mck-button-border {\n    border: 2px solid $border;\n    border-radius: 0;\n}\n\n@mixin mck-thin-button-border {\n    border: 1px solid $background-checkbox-disabled;\n    border-radius: 0;\n}\n\n@mixin mck-content-border {\n    border: 1px solid $border;\n}\n\n@mixin mck-content-border-light {\n    @include mck-content-border;\n    border-color: $selected-option;\n}\n\n@mixin mck-card-content {\n    @include mck-content-border;\n    background-color: $results-list-background-color;\n}\n\n@mixin mck-card-content-light {\n    @include mck-content-border-light;\n    background-color: $results-list-background-color;\n}\n\n\n// functions\n\n/**\n * Makes text have maximum lines with ellipsis. Works on most modern browsers except\n * of Firefox..\n */\n@mixin max-lines($count) {\n    display: -webkit-box;\n    overflow: hidden;\n    -webkit-line-clamp: $count;\n    -webkit-box-orient: vertical;\n}\n\n// images\n@mixin mck-icon($width, $asset, $height: $width,  $assetPath: \"libs/ui/src/lib/icons\",) {\n    width: $width;\n    height: $height;\n    background-image: url(\"#{$assetPath}/#{$asset}\");\n    background-size: contain;\n}\n\n\n@mixin mck-small-icon($mobile-asset, $desktop-asset: $mobile-asset) {\n    @include mck-icon(16px, $asset: $mobile-asset);\n\n    @include is-screen-size(not-mobile) {\n        @include mck-icon(24px, $asset: $desktop-asset);\n    }\n}\n\n// skeleton\n@mixin skeleton-bg {\n    background-color: $selected-option;\n}\n\n@mixin placeholder-image-size {\n    width: 81px;\n    height: 82px;\n\n    @include is-screen-size(desktop) {\n        width: 219px;\n        height: 119px;\n    }\n}\n\n@mixin placeholder-status-size {\n    width: 117px;\n    height: 78px;\n\n    @include is-screen-size(desktop) {\n        width: 117px;\n        height: 78px;\n    }\n}\n","@import \"mixins\";\n@import \"vars\";\n@import \"~@angular/material/theming\";\n\n.mck {\n    &-inherit {\n        &-font {\n            // patches global font styling done by legacy ui\n            font: inherit;\n        }\n    }\n\n    &-content {\n        &-expand {\n            width: 100%;\n            height: 100%;\n\n            &-width {\n                width: 100%;\n            }\n\n            &-height {\n                height: 100%;\n            }\n        }\n\n        &-with-footer-height {\n            height: calc(100vh - 58px);\n        }\n\n        &-full-height {\n            height: 100vh;\n        }\n\n        &-page {\n            @extend .mck-content-expand;\n            background: $page-background-color;\n\n            &-container {\n                @extend .mck-content-full-height;\n            }\n        }\n\n        &-body {\n            width: 100%;\n            display: block;\n            @include is-screen-size(not-mobile) {\n                max-width: 1024px;\n            }\n        }\n\n        &-group {\n            @include page-padding;\n            width: 100%;\n\n            @include is-screen-size(not-mobile) {\n                max-width: 759px;\n            }\n        }\n    }\n\n    &-shadow {\n        @include mat-elevation(1);\n    }\n\n    &-flex-1 {\n        flex: 1;\n    }\n\n    &-row {\n        display: flex;\n        flex-direction: row;\n\n        &.mck-center {\n            &-justify {\n                justify-content: center;\n            }\n\n            &-align {\n                align-items: center;\n            }\n\n            @extend .mck-center-justify;\n            @extend .mck-center-align;\n        }\n\n        &.mck-stretch {\n            align-items: stretch;\n        }\n\n        &-flow {\n            @extend .mck-row;\n            flex-wrap: wrap;\n        }\n\n        &.mck-scroll {\n            overflow-x: scroll;\n        }\n    }\n\n    &-column {\n        display: flex;\n        flex-direction: column;\n\n        &.mck-center {\n            justify-content: center;\n            align-items: center;\n        }\n\n        &.mck-space-between {\n            justify-content: space-between;\n        }\n\n        &.mck-stretch {\n            align-items: stretch;\n        }\n\n        &.mck-scroll {\n            overflow-y: scroll;\n            overflow-y: overlay;\n        }\n\n        &.mck-flex-end {\n            justify-content: flex-end;\n        }\n    }\n\n    &-overflow {\n        overflow: auto;\n    }\n\n    &-bordered, &-input {\n        border-radius: 0;\n        border: 1px solid $border;\n\n        &:hover {\n            border-color: #464646;\n        }\n\n        &:focus {\n            border-color: $primary;\n        }\n\n        &[disabled=\"true\"] {\n            pointer-events: none;\n            border-color: #f0f0f0;\n            color: #ccc;\n        }\n    }\n\n    &-date-display {\n        position: relative;\n        display: flex;\n        align-items: center;\n        width: 100%;\n        height: 2.5em;\n        padding: $spacing-small $spacing-xxsmall;\n        transition: border 200ms, box-shadow 200ms;\n    }\n\n    &-form-input {\n\n        .mat-form-field-wrapper {\n            padding-bottom: $spacing-xxsmall;\n        }\n\n        mat-form-field {\n            min-width: auto !important;\n\n            .mat-form-field-infix {\n                width: 100%;\n                padding-top: $spacing-xxsmall !important;\n                padding-bottom: $spacing-xxsmall !important;\n            }\n\n            .mat-form-field-outline {\n                min-height: 2.5rem;\n            }\n\n            &.mck-center-suffix {\n                .mat-form-field-infix {\n                    @extend .mck-row;\n                    @extend .mck-center;\n                }\n            }\n\n            &.mck-select-dropdown-button {\n                .mat-form-field-infix {\n                    margin-top: 4px;\n                }\n\n                .mat-form-field-flex {\n                    @include is-screen-size(mobile) {\n                        padding-left: $spacing-xxsmall;\n                        padding-right: $spacing-xxsmall;\n                    }\n                }\n\n                @include is-screen-size(mobile) {\n                    .mat-form-field-outline, .mat-form-field-infix {\n                        height: 2rem;\n                        min-height: 2rem;\n                    }\n\n                    .mat-form-field-infix {\n                        padding-top: 0 !important;\n                        padding-bottom: 0 !important;\n                    }\n                }\n            }\n        }\n\n        mat-form-field.mat-s {\n            @include form-label;\n            text-align: left;\n        }\n\n        mat-select {\n            margin-top: 0.2rem;\n\n            .mat-select-arrow {\n                visibility: hidden;\n            }\n\n            & .mat-select-value > .mat-select-value-text > span {\n                @include form-label;\n                font-weight: 400;\n            }\n        }\n\n        .mat-form-field-outline {\n            & > .mat-form-field-outline-start.mat-form-field-outline-start,\n            & > .mat-form-field-outline-gap.mat-form-field-outline-gap,\n            & > .mat-form-field-outline-end.mat-form-field-outline-end {\n                border-color: $border;\n                border-width: 1px;\n                border-radius: 0;\n            }\n        }\n\n        .available {\n            .mat-form-field-outline {\n                & > .mat-form-field-outline-start.mat-form-field-outline-start,\n                & > .mat-form-field-outline-gap.mat-form-field-outline-gap,\n                & > .mat-form-field-outline-end.mat-form-field-outline-end {\n                    border-color: #3aa537;\n                }\n            }\n        }\n\n        .booked {\n            .mat-form-field-outline {\n                & > .mat-form-field-outline-start.mat-form-field-outline-start,\n                & > .mat-form-field-outline-gap.mat-form-field-outline-gap,\n                & > .mat-form-field-outline-end.mat-form-field-outline-end {\n                    border-color: #e12e41;\n                }\n            }\n        }\n\n        .mat-form-field-outline-thick {\n            & > .mat-form-field-outline-start.mat-form-field-outline-start,\n            & > .mat-form-field-outline-gap.mat-form-field-outline-gap,\n            & > .mat-form-field-outline-end.mat-form-field-outline-end {\n                border-color: $primary;\n                border-width: 1px;\n                border-radius: 0;\n            }\n        }\n\n        > mat-form-field.mck-error {\n            .mat-form-field-wrapper {\n                // less jumping for error message.\n                padding-bottom: 0;\n            }\n\n            .mat-form-field-outline {\n                & > .mat-form-field-outline-start.mat-form-field-outline-start,\n                & > .mat-form-field-outlin